	module.exports = {"data":{"markdownRemark":{"html":"<h2>Ceremony</h2>\n<p>We will be holding a small ceremony with immediate family with a reception to follow.</p>\n<h2>Reception</h2>\n<p><strong>NOTE: This will be an ADULT ONLY celebration.</strong></p>\n<p>You can think of our reception as a classy backyard cocktail party.</p>\n<p>We will have a big tent set up with a variety of table games and yard games, along with an open bar that will include wine, beer, liquor, soft drinks, and water. Snacks and endless appetizers will be offered all evening long. Please come hungry as there will be plenty of food!</p>\n<hr>\n<h2>What to wear</h2>\n<p>As we said, you can think of our reception as a classy backyard cocktail party. We recognize that dress in this situation, especially in the middle of summer, is a difficult question for our male guests. So, men, please use this simple guide as a reference:</p>\n<img src=\"https://static1.businessinsider.com/image/5751b32991058427008c69f5-1041/jpmorgandresscodeskitch.jpg\" />\n<p>Credit: <a href=\"https://www.businessinsider.com/jp-morgans-relaxed-dress-code-2016-6\">Business Insider</a></p>\n<hr>\n<h2>Location and parking</h2>\n<p>The ceremony and reception will be held at 1812 State Route 14A in Penn Yan, NY. Guests may follow the driveway to the top of the hill and continue to follow the signs for parking in the backyard. The reception will be held outdoors under a large big-top tent. Assistance will be provided to those with disabilities or those who have trouble walking. Please check the website for additional instructions in the event of rain. </p>","frontmatter":{"title":"Wedding Day"},"fields":{"slug":"/wedding-day"},"excerpt":"Ceremony We will be holding a small ceremony with immediate family with a reception to follow. Reception NOTE: This will be an ADULT ONLY…"}},"pathContext":{"slug":"/wedding-day"}}

